angular.module('LeagueController', []).controller('LeagueController', function ($scope, $q, $view, $uibModal, $confirm, $location, $state, AuthenticationService, LeagueService) {

    let vm = this;

    //Current User
    vm.currentUser = {};
    let getCurrentUser = AuthenticationService.currentUser();
    getCurrentUser.then((response) => {
        vm.currentUser = response;
    }, (error) => {
        console.log(error);
    });
    
    //Current League
    vm.league = {};
    let getLeague = LeagueService.getLeague();
    getLeague.then((response) => {
        vm.league = response;
    }, (error) => {
        console.log(error);
    });
    
    //Commish Auth
    vm.isCommish = () => {
        if (vm.currentUser._id == vm.league.commish_id[0]) {
            return true;
        }
        else {
            return false;
        }
    }

    
    //Create New League
    vm.createNewLeague = function () {
        var newLeagueInfo = {
            "leagueName": vm.newLeague.leagueName,
            "leagueSize": vm.newLeague.leagueSize,
            "user_id": vm.currentUser._id
        };
        return LeagueService.createNewLeague(newLeagueInfo)
            .then((response) => {
                $state.transitionTo('dashboard');
            }, (error) => {
                console.log(error);
            });
    };

    //Join League
    vm.joinLeague = function () {
        let code = "";
        for (var x in vm.data) {
            code += vm.data[x];
        }
        return LeagueService.joinLeague(code)
            .then((response) => {
                $state.transitionTo('dashboard');
            }, (error) => {
                console.log(error);
            });
    };
    vm.moveFocus = (nextId) => {
        $('#' + nextId).focus();
    };

    //New League Message
    vm.newMessage = () => {
        console.log("New Message");
        // let parentElem = parentSelector ? angular.element($document[0].querySelector('.modal-demo ' + parentSelector)) : undefined;
        vm.newMessageModal = true;
        // return $uibModal.open({
        //     animation: true,
        //     templateUrl: "newMessageModal",
        //     ariaLabelledBy: 'modal-title',
        //     ariaDescribedBy: 'modal-body',
        //     // appendTo: parentElem,
        //     // component: 'modalComponent',
        //     // resolve: {
        //     //     items: function () {
        //     //         return $ctrl.items;
        //     //     }
        //     // }
        // });

    }
});